import React from "react";
import "./EventList.scss";
import EventCard from "./EventCard";
import DailyNoteCard from "./DailyNoteCard";
import { AuthorizedLocation } from "../../orbital-interfaces/authorized-items";
import "./EventListSection.scss";
import { EventInformation } from "../../orbital-interfaces/TEC";
import { DailyNotes } from "../../orbital-interfaces/DailyNotes";
import { EventEditAuthorization } from "../EditEvent/EditEventAuthorization";

interface EventListSectionProps {
  locations: AuthorizedLocation[];
  events: EventInformation[];
  notes: DailyNotes[];
  header: string;
  showEmployeeNames: boolean;
  showTotalDuration: boolean;
  onEdit: (ev: EventInformation, eventEditAuth: EventEditAuthorization) => void;
}

// --------------------------------------------------------------------------------------
const EventListSection: React.FC<EventListSectionProps> = ({
  locations,
  header,
  events,
  notes,
  showEmployeeNames,
  onEdit,
}: EventListSectionProps) => {
  const multiLocation =
    locations.filter((l) =>
      l.departments.filter((d) => d.positions.filter((p) => p.hasPosition))
    ).length > 1;

  if (events.length === 0) {
    return <div />;
  }

  return (
    <>
      <div className="header">{header}</div>
      <div className="daily-notes-list">
        {notes.map((note) => (
          <DailyNoteCard
            key={note.DailyNoteId}
            note={note}
            location={locations.filter((l) => l.id === note.LocationId)[0]}
          />
        ))}
      </div>
      <div className="event-list">
        {events.map((ev) => (
          <EventCard
            onEdit={onEdit}
            key={ev.ScheduleEventId}
            event={ev}
            location={locations.filter((l) => l.id === ev.LocationId)[0]}
            multiLocation={multiLocation}
            showEmployeeName={showEmployeeNames}
          />
        ))}
      </div>
    </>
  );
};

export default EventListSection;
