import { Message } from "../orbital-interfaces/Message";
import { ApplicationUser } from "../context/ApplicationUserContext";
import apiHelper from "./OrbitalApiHelper";

// ----------------------------------------------------------------------------------------------
const getUserMessage = async (user: ApplicationUser, msgId: number) => {
  return (await apiHelper.get(
    user,
    "api/v2/employee/messages/" + msgId
  )) as Message;
};

// ----------------------------------------------------------------------------------------------
const getUserMessages = async (user: ApplicationUser) => {
  return (await apiHelper.get(user, "api/v2/employee/messages")) as Message[];
};

// ----------------------------------------------------------------------------------------------
const getReadCount = async (user: ApplicationUser, msgId: number) => {
  return (await apiHelper.get(
    user,
    "api/v1/employee/messages/" + msgId + "/readCount"
  )) as number;
};

// ----------------------------------------------------------------------------------------------
const setIsPinned = async (
  user: ApplicationUser,
  msgId: number,
  isPinned: boolean
) => {
  return await apiHelper.post(
    user,
    "api/v1/employee/messages/" + msgId + "/togglePin",
    { isPinned }
  );
};

// ----------------------------------------------------------------------------------------------
const setIsViewed = async (
  user: ApplicationUser,
  msgId: number,
  isViewed: boolean
) => {
  return await apiHelper.post(
    user,
    "api/v1/employee/messages/" + msgId + "/toggleView",
    { isViewed }
  );
};

// ----------------------------------------------------------------------------------------------
const deleteMessage = async (user: ApplicationUser, msgId: number) => {
  return await apiHelper.del(user, "api/v1/employee/messages/" + msgId);
};

export default {
  getUserMessage,
  getUserMessages,
  getReadCount,
  setIsPinned,
  setIsViewed,
  deleteMessage,
};
