/* eslint-disable */
import React, { useState, useEffect, useContext } from "react";
import { AuthorizedItems, Role } from "../orbital-interfaces/authorized-items";
import db from "../data/dbStore";
import UserAPI from "../api/UserAPI";
import {
  ApplicationUser,
  ApplicationUserContext,
} from "./ApplicationUserContext";

export interface IAuthorizedItemsContext {
  auth: AuthorizedItems;
  refreshAuth: (
    force: boolean,
    newUser: ApplicationUser
  ) => Promise<AuthorizedItems | null>;
  clearAuth: () => void;
  authLoading: boolean;
}

const INITIAL_AUTHORIZED_ITEMS: AuthorizedItems = {
  employeeId: 0,
  role: Role.Employee,
  locations: [],
  loading: false,
  payrollEvents: [],
  tags: [],
};

const INITIAL_AUTHITEMS_CONTEXT = {
  auth: INITIAL_AUTHORIZED_ITEMS,
  refreshAuth: (force: boolean, newUser: ApplicationUser) => {
    return new Promise<AuthorizedItems | null>(() => {});
  },
  clearAuth: () => {},
  authLoading: false,
};

export const AuthorizedItemsContext = React.createContext<
  IAuthorizedItemsContext
>(INITIAL_AUTHITEMS_CONTEXT);

// ----------------------------------------------------------------------------------------------
async function getAuthItems(
  currentAuth: AuthorizedItems,
  user: ApplicationUser,
  force: boolean
) {
  if (currentAuth.employeeId !== 0 && !force) {
    return currentAuth;
  }

  if (!force) {
    const authFromStore = await db.getAuthItems();
    if (
      authFromStore &&
      authFromStore.locations &&
      authFromStore.locations.length > 0
    ) {
      return authFromStore;
    }
  }

  const authFromApi = await UserAPI.getAuthorizedItems(user);
  if (
    authFromApi &&
    authFromApi.locations &&
    authFromApi.locations.length > 0
  ) {
    db.putAuthItems(authFromApi).then(() =>
      console.log("auth items stored in indexdb")
    );
    return authFromApi;
  } else {
    console.log("got nothing from auth api");
    return null;
  }
}

export const AuthorizedItemsContextProvider = (props: any) => {
  const [auth, internalSetAuth] = useState({ ...INITIAL_AUTHORIZED_ITEMS });
  const [authLoading, setLoading] = useState(false);
  const { user } = useContext(ApplicationUserContext);

  // ----------------------------------------------------------------------------------------------
  useEffect(() => {
    let mounted = true;
    setLoading(true);

    if (!user || !user.isLoggedin) {
      return;
    }

    getAuthItems(auth, user, false).then((newAuth) => {
      if (!mounted) {
        return;
      }

      setLoading(false);

      if (newAuth) {
        internalSetAuth(newAuth);
      }
    });

    return () => {
      mounted = false;
    };
  }, [user, auth]);

  // ----------------------------------------------------------------------------------------------
  const refreshAuth = async (force: boolean, newUser: ApplicationUser) => {
    setLoading(true);

    const newAuth = await getAuthItems(auth, newUser, force);
    setLoading(false);
    if (newAuth) {
      internalSetAuth(newAuth);
    }

    return newAuth;
  };

  // ----------------------------------------------------------------------------------------------
  const clearAuth = () => {
    internalSetAuth(INITIAL_AUTHORIZED_ITEMS);
    db.putAuthItems(INITIAL_AUTHORIZED_ITEMS).then(() => {
      console.log("Cleared Authorized Items from db");
    });
  };

  return (
    <AuthorizedItemsContext.Provider
      value={{
        auth,
        refreshAuth,
        clearAuth,
        authLoading,
      }}
    >
      {props.children}
    </AuthorizedItemsContext.Provider>
  );
};
// ---------------------------------------------------------------------------------------------
