import { EmployeeSort } from "../orbital-interfaces/authorized-items";
import { Employee } from "../orbital-interfaces/Employee";
import { ApplicationUser } from "../context/ApplicationUserContext";
import { formatDateTimeAsMvcString } from "../util/time-helper";
import apiHelper from "./OrbitalApiHelper";

const getEmployeesByLocationIdPositionIdAndActiveDateRange = async (
  user: ApplicationUser,
  locId: number,
  posId: number,
  start: Date,
  end: Date,
  sort: EmployeeSort
) => {
  const data = {
    locId,
    posId,
    start: formatDateTimeAsMvcString(start),
    end: formatDateTimeAsMvcString(end),
    sort,
  };

  return (await apiHelper.post(
    user,
    "api/v2/employees/at-position",
    data
  )) as Employee[];
};

export default {
  getEmployeesByLocationIdPositionIdAndActiveDateRange,
};
